.header {
  background-color: #fefefe;
  height: 60px;
  line-height: 50px;
  width:100%;
  padding-left:20px;
  border:1px solid rgb(233, 233, 233);
}
.header h1 {
  color:#000;
  font-size: 20px;
  font-weight: bold;
  margin-left:15px;
  margin-top:5px;
}

.history {
  padding: .8rem 0 .8rem 1.5rem;
  border-bottom:1px solid rgb(233, 233, 233);
}

.collapsedBtn {
  font-size: 22px;
  margin-right:20px;
  margin-left:20px;
  color: rgb(29, 107, 185);
  margin-top:20px;
}

.logo {
  height: 32px;
  margin-top:12px;
}